export default {
	data() {
		return {
			topTabState:'0',
			paegsize:1,//触底加载更多
			index:11,
			pageInfo:[]
		};
	},
	onLoad() {
		let _this = this;
		_this.getQuesition()
	},
	onReachBottom() {
		console.log('onReach');
		this.getQuesition('onReach');
	},
	methods: {
		// 获取问答列表
		getQuesition(page='0'){
			var _this = this;
			var oldpageInfo = _this.pageInfo;
			if(page === 'onReach'){
				uni.request({
					url:shoppublic.getUrl() + 'questions/findQuestionsList',
					data:{
						token:6239,
						type:1,
						updatetime:'',
						pagesise:_this.index,
						province_id:610000,
						city_id:610100,
						questions:''
					},
					success:(res) => {
						if(page === 'onReach' && res.data.responseBody){
							oldpageInfo = oldpageInfo.concat(res.data.responseBody);
							_this.index += 7;
						}else{
							return false;
						}
						_this.pageInfo = oldpageInfo;
						console.log(_this.pageInfo);
					}
				})
				this.paegsize += 10;
			}else{
				uni.request({
					url:shoppublic.getUrl() + 'questions/findQuestionsList',
					data:{
						token:6239,
						type:1,
						updatetime:'',
						pagesise:_this.paegsize,
						province_id:610000,
						city_id:610100,
						questions:''
					},
					success:(res) => {
						_this.pageInfo= res.data.responseBody;
						console.log(_this.pageInfo);
					}
				})
			}
		},
		
		// 顶部选项卡切换
		tabChanges(state){
			this.topTabState = state;
		},
		//跳转搜索:1是问答跳转
		toSearch(e) {
			uni.navigateTo({
				url: '../searchPage/searchPage?search_state=' + '1'
			});
		},
		toQuestionAndAnswerDetails(id){
			uni.navigateTo({
				url: './questionAndAnswerDetails/questionAndAnswerDetails?id=' + id
			});
		}
	},
	// 原生按钮点击事件
	onNavigationBarButtonTap(e) {
		let _this = this;
		if(e.index === 0){//跳转问答发布
			
		}
		if (e.index === 1) {
			_this.toSearch();//跳转搜索
		}
	}
};
